import uniStatusBar from "@/components/uni-ui/uni-status-bar/uni-status-bar.vue"
import validCode from "@/components/common/vaildCode.vue"
import {mapMutations} from 'vuex';
export default {
	components: {
		uniStatusBar,validCode
	}, 
	data() {
		return {
			username:"",
			password:"",
			check:false,
			vaildcode:'',
			rules:{
				
				username:[
					{
						rule:/^1[3456789]\d{9}$/,
						msg:"手机号格式不正确"
					}
				],
				password:[
					{
						rule:/^.{5,20}$/,
						msg:"密码长度必须为5-20个字符"
					}
				]
				
			},
			focusClass:{
				username:false,
				password:false,
			},
		}
	},
	methods: {
		...mapMutations(['login']),
		goBack(){
			uni.navigateBack({
				delta: 1
			});
		},
		getCode(val){
			this.vaildcode = val
		},
		validate(key){
			var check = true
			this.rules[key].forEach((v)=>{
				if (!(v.rule).test(this[key])) {
					uni.showToast(
						{ 
							title: v.msg, 
							icon: 'none'
						}
					)
					check = false
					return false
				}
			})
			return check
		},
		submit(){
			if (!this.check) {
				return uni.showToast({
					title: '请先同意芒果点餐应用协议',
					icon: 'none'
				});
			}
			if (!this.validate('username')) return;
			if (!this.validate('password')) return;
			uni.showLoading({
				title: '注册中...',
				mask: true
			})
			this.$H.post('/user/user/register',{
				username:this.username,
				password:this.password,
				vaildcode:this.vaildcode
			}).then(res=>{
				uni.hideLoading()
				uni.showToast({
					title: '注册成功',
					icon: 'none'
				});
				uni.navigateBack({
					delta:1
				})
			}).catch(err=>{
				
			})
		},
		openVaild(){
			uni.showModal({
				content:'默认为 1234'
			})
		},
		forget(){
			
		},
		focus(key){
			this.focusClass[key] = true
		},
		blur(key){
			this.focusClass[key] = false
		}
	}
}
